<style scoped>
    
    .layout{
        border: 1px solid #d7dde4;
        background: #f5f7f9;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
    }
    .layout-header-bar{
        background: #fff;
        box-shadow: 0 1px 1px rgba(0,0,0,.1);
    }
    .ivu-layout-sider{
      background:#fff;
    }
    .ivu-layout-sider-children{
      border-right:1px solid #ddd;
    }
    .ivu-menu-light{margin-right:-1px;}
    .ivu-menu-light.ivu-menu-vertical .ivu-menu-item a{color:#888;}
    .ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) a{
      color:#2d8cf0;
    }
</style>
<template>
    <div class="layout" id="admin">
        <Sider :style="{position: 'fixed', height: '100vh', left: 0, overflowX: 'hidden',overflowY:'scroll'}">
            <div style="padding:20px;"><img style="width:40px;height:40px;border-radius:4px;vertical-align:middle;" src="/static/images/logo.png"><b style="margin-left:20px;font-size:20px;vertical-align:middle;">牛票票</b></div>
            <Menu active-name="1-2" theme="light" width="auto" :open-names="['1']">
                <Submenu name="1">
                    <template slot="title">
                      <Icon type="person-stalker"></Icon>
                        用户管理
                    </template>
                    <MenuItem name="userList"><router-link to="/userList">用户列表</router-link></MenuItem>
                </Submenu>
                <Submenu name="2">
                    <template slot="title">
                        <Icon type="navicon-round"></Icon>
                        内容管理
                    </template>
                    <MenuItem name="AuditTopicList"><router-link to="/AuditTopicList">H帖子审核列表</router-link></MenuItem>
                    <MenuItem name="TopicList"><router-link to="/TopicList">帖子列表</router-link></MenuItem>
                    <MenuItem name="HitTopicAuditList"><router-link to="/HitTopicAuditList">中奖审核列表</router-link></MenuItem>
                    <MenuItem name="RecomandList"><router-link to="/RecomandList">首页推荐列表</router-link></MenuItem>
                    <MenuItem name="ReplayManager"><router-link to="/ReplayManager">评论管理</router-link></MenuItem>
                    <MenuItem name="addArticle"><router-link to="/addArticle">内容补发</router-link></MenuItem>
                </Submenu>
                <Submenu name="3">
                    <template slot="title">
                       <Icon type="ribbon-b"></Icon>
                        全局推荐
                    </template>
                    <MenuItem name="manage"><router-link to="/manage">公共管理</router-link></MenuItem>
                    <MenuItem name="recomandPic"><router-link to="/recomandPic">图文推荐</router-link></MenuItem>
                </Submenu>
                <Submenu name="4">
                    <template slot="title">
                       <Icon type="clipboard"></Icon>
                        版面管理
                    </template>
                    <MenuItem name="panelList"><router-link to="/panelList">版面列表</router-link></MenuItem>
                    <MenuItem name="moderatoerList"><router-link to="/moderatoerList">版主列表</router-link></MenuItem>
                </Submenu>
                <Submenu name="5">
                    <template slot="title">
                       <Icon type="social-android"></Icon>
                        Android APP
                    </template>
                    <MenuItem name="APK"><router-link to="/APK">APK文件</router-link></MenuItem>
                    <MenuItem name="addPackage"><router-link to="/addPackage">增量包</router-link></MenuItem>
                </Submenu>
                <Submenu name="6">
                    <template slot="title">
                       <Icon type="android-settings"></Icon>
                        系统管理
                    </template>
                    <MenuItem name="Account"><router-link to="/Account">后台账户</router-link></MenuItem>
                    <MenuItem name="settings"><router-link to="/settings">全局配置</router-link></MenuItem>
                    <MenuItem name="exit"><router-link to="/exit">退出系统</router-link></MenuItem>
                </Submenu>
            </Menu>
        </Sider>
        <Layout :style="{marginLeft: '200px',minHeight:'calc(100vh - 3px)',overflow:'hiden'}">
            <Header :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}">
                <div class="ub ub-ac">
                    <div class="ub-f1"></div>
                <div><Avatar src="https://i.loli.net/2017/08/21/599a521472424.jpg" /> <b>胡吃海喝</b></div>
                <div class="ml-20"><Icon type="email" size="24" style="vertical-align:middle"></Icon> <b>我的消息</b></div>
                <div class="ml-20"><Icon type="android-exit" size="24" style="vertical-align:middle"></Icon> <b>退出</b></div>
                </div>
            </Header>
            <Content :style="{padding: '0 16px 16px'}">
                <Card style="margin-top:20px;">
                    <router-view/> 
                </Card>
            </Content>
        </Layout>
    </div>
</template>
<script>
export default {
  name: 'admin',
  data(){
    return {
        
    }
  }
}
</script>


