<template>
    <div id="addArticle">
        <Form :label-width="80">
            <FormItem label="补发标题">
                <Input v-model="article.Title" placeholder="请输入标题"></Input>
            </FormItem>
            <FormItem label="补发内容">
                <Input v-model="article.Content" type="textarea" :autosize="{minRows: 5,maxRows: 10}" placeholder="请输入内容"></Input>
            </FormItem>
            <FormItem label="补发用户">
                 <Select v-model="article.Auther" filterable>
                    <Option v-for="item in userList" :value="item.Uid" :key="item.Uid">{{ item.Name }}</Option>
                </Select>
            </FormItem>
            <FormItem label="推荐语">
                <Input v-model="article.recommendText"  placeholder="请输入推荐语"></Input>
            </FormItem>
            <FormItem label="帖子状态">
               <Select v-model="article.State">
                    <Option v-for="(value,key) in articleState" :key="key" :value="key" >{{value}}</Option>
                </Select>
            </FormItem>
            <FormItem label="补发时间">
                <Row>
                    <Col span="11">
                        <DatePicker v-model="dateTime.day" type="date" placeholder="Select date"></DatePicker>
                    </Col>
                    <Col span="2" style="text-align: center">-</Col>
                    <Col span="11">
                        <TimePicker v-model="dateTime.time" type="time" placeholder="Select time"></TimePicker>
                    </Col>
                </Row>
            </FormItem>
             <FormItem>
                <Button type="primary" @click="addArticle()">补发</Button>
            </FormItem>
        </Form>
    </div>
</template>

<script>
export default {
name:"addArticle",
data:()=>{
    return {
        articleState:{0:'普通',1:'推荐',2:'下沉',3:'被删除',4:'审核未过',},
        userList:[],
        dateTime:{
            day:"",
            time:""
        },
        article:{}
    }
},
methods:{
    searchUser:function(){
        var t=this;
        $.ajax({
            url:t.base.baseUrl+"/searchUserName",
            data:{name:t.article.Auther},
            type:"get",
            success:function(d){
                t.userList=d.Data;
            }
        })
    },
    addArticle:function(){
        var t=this;
        if(t.dateTime.day!=""){
            t.article.Created=t.dateTime.day.toLocaleDateString()+t.dateTime.time;
        }
        $.ajax({
             url:t.base.baseUrl+"/addArticle",
            data:{name:t.article.Auther},
            type:"get",
            success:function(d){
                t.userList=d.Data;
            }
        })
    }
},created:function(){
    this.searchUser();
}
}
</script>

<style>

</style>



